@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

body {
    background-color: black;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    color:chartreuse;
    padding:0;
    margin:0;
}

#historyframe, #typeframe {
    width:100%;
}
#historyframe {
    margin-bottom:50px;
}
#typeframe {

    flex-grow: 1;
    position: fixed;
    left:0px;
    bottom:0;
    background-color: black;
    border-top: 1px solid chartreuse;

}
#typearea {
    color:white;
    padding:15px;
    outline:0;
}
.mainframe {
    padding:15px;
}
.sendbtn {
    width:40px;
    height:40px;
    background-color: chartreuse;
    color:black;
    position:absolute;
    right:20px;
    top:-40px;
    border:0;
    font-size: 24px;
}